import {getStore, updateStore} from './store'
 
function reducer  (action){
    switch(action.type){
        case "delete":
            const store = getStore(); 
            const deletIndex = action.payload.index;         
            const newStore = [...store.slice(0, deletIndex), ...store.slice(deletIndex + 1)];
            updateStore(newStore);
            action.cb();
        case "edit": 
            const currentStore = getStore();       
            const editedTask = action.payload;
            const editedIndex = getStore().findIndex(task => task.id === editedTask.id)    
            const updateTask = [...currentStore.slice(0, editedIndex),...currentStore.slice()];
            currentStore[editedIndex] = editedTask;
            updateStore(currentStore);
            action.cb();
        case "add": 
            const addStore = getStore();       
            const newTask = action.payload; 
            const addTask = [...addStore,newTask];
            updateStore(addTask);
            console.log(getStore());
            action.cb();
            default: return  getStore()
          //  case "delete": return "Delete Task";
    }
}
export default reducer 